import {
  createGlobalStyle,
  Preflight,
  ThemeProvider
} from '@xstyled/styled-components'
import React from 'react'

import theme from '../../constants/theme'

const GlobalStyle = createGlobalStyle` 
  #___gatsby {
    margin-top: 3.375rem;
  }

  @media (min-width: 768px) {
    #___gatsby {
      margin-top: 3.75rem;
    }
  }

  #___gatsby{
    color: #292929;

    --gp-orange: #F16E42;

    .climate {
      --theme-primary: #0BDDB4;
      --theme-secondary: #C0F6EC;
      --link: #18ADA1;
    }
    .fashion {
      --theme-primary: #fea198;
      --theme-secondary: #C0F6EC;
      --link: #fea198;
    }
    .forest {
      --theme-primary: #d1bdda;
      --theme-secondary: #C0F6EC;
      --link: #d1bdda;
    }
  }
  a {
    color: var(--link);
    text-decoration: underline;
  }
`

const Layout = ({ children }) => (
  <ThemeProvider theme={theme}>
    <Preflight />
    <GlobalStyle />
    {children}
  </ThemeProvider>
)

export default Layout
